import React, { Component } from 'react';
import './Harrison.css';
import '../../App.css';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';

const TITLE = 'People | Harrison Lab'

const courseList = [
  { courseTitle: "Organic Chemistry", courseCode: "CHEM 2OA3", courseDesc: "An introduction to organic chemistry with emphasis on the reactions of functional groups and an introduction to spectroscopic techniques for structure determination.", courseLink: "https://macdrive.mcmaster.ca/f/2f1706f9e34c4d929198/" },
  { courseTitle: "Applications of Spectroscopy: Structural Elucidation and Small Molecules", courseCode: "CHEMBIO 3OB3", courseDesc: "Applications of spectroscopy detailing the use of NMR, MS, IR, and UV in determining structures of small molecules and biomolecules with a particular focus on natural products.", courseLink: "https://macdrive.mcmaster.ca/f/00b458a8438046229e5a/" },
  { courseTitle: "Natural Products", courseCode: "CHEM/CHEMBIO 4OA3", courseDesc: "A description of basic building blocks and reaction mechanisms involved in the biosynthesis of naturally occurring compounds. This course is an extension of basic organic chemistry as applied to the mechanisms by which natural products (secondary metabolites) are biosynthesized.", courseLink: "https://macdrive.mcmaster.ca/f/44d4d8fabe37434fa1a8/" },
  { courseTitle: "Organic Mechanistic Tools for Chemical Biology", courseCode: "CHEMBIO 3OA3", courseDesc: "Chemistry and biology of primary metabolism. Synthesis, biosynthesis and degradation of carbohydrates, nucleotides, and proteins are compared and contrasted by studying reaction mechanisms and catalysis.", courseLink: "https://macdrive.mcmaster.ca/f/74e2f1094cd049228be0/" },
  { courseTitle: "Pharmaceutical Chemistry Laboratory I: Biomolecular Interactions", courseCode: "CHEMBIO 2L03", courseDesc: "Students will be introduced to the standard tools and techniques employed in Chemical Biology research.", courseLink: "" },
  { courseTitle: "Chemical Biology Laboratory II", courseCode: "CHEMBIO 3L03", courseDesc: "A research project will be formulated and addressed using the tools of Chemical Biology.", courseLink: "" },
  { courseTitle: "Medicinal Chemistry: Drug Design and Development", courseCode: "CHEMBIO 4OB3", courseDesc: "Topics will include lead compound discovery strategies; high-throughput screening and ‘in silico’ screening; exploration of structure-activity relationships; drug targets and molecular mechanisms of drug action; strategies for drug optimization.", courseLink: "" },
  { courseTitle: "Survey of Astrobiology", courseCode: "ORIG 701 | Graduate", courseDesc: "A current survey of the three major aspects of Astrobiology and the Origins of Life: astrophysics and planetary science, geochemistry and geology, and molecular biology and biochemistry.  The course introduces students to the major questions and results of Astrobiology, and develops interdisciplinary approaches toward their solution.", courseLink: "" },
  { courseTitle: "Bio-Organic Chemistry", courseCode: "CHEM 758 | Graduate", courseDesc: "An introduction to natural products and their biosynthesis, as well as the techniques used to determine biosynthetic pathways experimentally. Metabolites derived from acetate are then examined. These include the fatty acids, prostaglandins and the arachidonic acid cascade, the polyketides and the terpenoids and steroids.", courseLink: "" }
]

class Harrison extends Component {
  render() {

    
    const HContact = 'Email: pharriso@mcmaster.ca \nOffice: ABB-418 \nLab: ABB-A201'
    let newHContact = HContact.split('\n').map(i => {
      return <p>{i}</p>
    });
    let HBio = "Paul Harrison was born in Northampton, UK, and developed his interest in chemistry from a chemistry set that evolved quickly into an entire lab in the family home. He attended Oundle School in Northamptonshire, and spent a “gap year” working for ICI in Runcorn, Cheshire on catalysis before going up to Oxford. After 3 years of study and punting on the Cherwell, he conducted his Part II fourth year research project with Professor (now Sir) Jack Baldwin, where he developed a synthesis of L-α-aminoadipic acid, an unusual amino acid which is involved in penicillin biosynthesis. He moved to Alberta, Canada for his Ph.D., in which he worked on polyene antifungal antibiotics in the group of Prof. John Vederas. After post-doctoral work on terpene biosynthesis at Brown in Providence, USA, Paul started his academic post at McMaster. His research work has been at the interface of chemistry and biology, and includes the development of biomimetic chemical models of natural product biosynthesis, the cellular assembly of natural products such as acyl-tetramic acids and β-lactones, and understanding the mechanisms by which antibiotics exert their biological effects."

    return (
      <div>

        <Helmet>
          <title>{ TITLE }</title>
        </Helmet>

        <div className="HBox">
          <section className="HProfile">
            <div className="HProf">
              <img className="HPic" src={require('../../assets/piprofile.png')}/>
            </div>
            <div className="HDescription">
              <h1>DR. PAUL H.M. HARRISON</h1>
              <div className="HBlock"></div>
              <h3>Associate Professor</h3>
              <h4>CHEMISTRY &#38; CHEMICAL BIOLOGY, MCMASTER UNIVERSITY</h4>
              <h3>Undergraduate Associate Chair</h3>              <h4>MCMASTER UNIVERSITY</h4>
              <h3 className="piSchool">B.A. (Oxford), Ph.D. (Alberta)</h3>
              <p1 className="HContact">{ newHContact }</p1>
              <a href="https://www.linkedin.com/in/paul-harrison-73452961/"> 
                <img className="HLinkedin" src={require('../../assets/linkedinicon.png')} width="20vw"/>
              </a>
            </div>
            <div className="HBio">
              <p1>{ HBio }</p1>
            </div>
          </section>
          <div className="HTeaching">
            <h1>TEACHING</h1>
            <div className="HBlock2"></div>
            <ul className="courseList">
              {courseList.map(
                (courseListing, index) => (
                  
                  <li key={index} className="courseListing">
                    <div className="courseTitle">
                      <h2>{ courseListing.courseTitle }</h2>
                      </div>
                    <div>
                      <h4 className="courseCode">{courseListing.courseCode}</h4>
                    </div>
                    <div className="courseDesc">
                      <p1>{courseListing.courseDesc}</p1>
                      {/* <p1> <a className="courseLink" href={courseListing.courseLink} target='_blank'>Read more.</a></p1> */}
                    </div>
                  </li>
                )
              )}
            </ul>
          </div>
        </div>
      </div>
    );
  }
}

export default Harrison;