import React, { Component } from 'react';
import './Header.css';
import { Link } from 'react-router-dom';



class Header extends Component {

  render() {
    
    return (

      <nav className="navbar" role="navigation" aria-label="main navigation">
        <div className="navbar-brand">
          <a>
            <Link to="/harrison">
              <img className="navlogo" src={require('../../assets/navlogo.png')} width="500"/>
            </Link>
          </a>
        
          <label
          role="button"
          className="navbar-burger burger"
          aria-label="menu"
          aria-expanded="false"
          htmlFor="nav-toggle-state"
          data-target="navbar-menu"
          >
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
          </label>
        </div>
        <input type="checkbox" id="nav-toggle-state" />

        
        <div className="navContainer navbar-menu">
          <div className="navbar-end">
            <div class="navbar-item has-dropdown is-hoverable">
              <Link class="navbar-link is-arrowless r-item" to="/harrison">RESEARCH</Link>
              <div class="navbar-dropdown is-hoverable">
                <Link class="navbar-item dropdownItem" to="/harrison/biosynthesis">BIOSYNTHESIS</Link>
                <Link class="navbar-item dropdownItem" to="/harrison/antibiotic-mechanisms-of-action">ANTIBIOTIC MECHANISMS OF ACTION</Link>
                <Link class="navbar-item dropdownItem" to="/harrison/biomimetic-synthesis">BIOMIMETIC SYNTHESIS</Link>
              </div>
            </div>

            <div class="navbar-item has-dropdown is-hoverable">
              <Link class="navbar-link is-arrowless r-item" to="/harrison/people">PEOPLE</Link>
              
              <div class="navbar-dropdown">
                <a>
                  <Link class="navbar-item dropdownItem" to="/harrison/people">GROUP MEMBERS</Link>
                </a>
                <a> 
                  <Link class="navbar-item dropdownItem" to="/harrison/pharrison">DR. P.H.M. HARRISON</Link>
                </a>
              </div>
            </div>
            

            <Link to="/harrison/publications" className="navbar-item r-item">PUBLICATIONS</Link>
        </div>
      </div>
    </nav>

    );
  }
}

export default Header;
