import React, { Component } from 'react';
import '../../App.css';
import './People.css';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';

const TITLE = 'People | Harrison Lab'


class People extends Component {

  constructor(props) {
    super(props) //since we are extending class Table so we have to use super in order to override Component class constructor
    this.state = { //state is by default an object
       members: [
          { year: 2019, name: 'Seto, A.', title: 'CHEMBIO 3RP3 Student', project: 'Claisen Self-condensation of Butyrolactone' },
          { name: 'Laleye, T.', title: 'CHEMBIO 4GG9 Student', project: 'Lactone Analogues of Pramanicin' },
          { name: 'Gorya, O.', title: 'CHEM 4G04 Student', project: 'Synthesis of Pramanicin Analogs: SAR' },
          { year: 2018, name: 'Peng, A.', title: 'CHEMBIO 4GG9 Student', project: 'Unsaturated Side-chain Lactone Analogues of Pramanicin' },
          { name: 'Bisson, V.', title: 'CHEMBIO 4GG9 Student', project: 'Fluoro-ebelactone B Production and Enzyme Inhibition' },
          { year: 2017, name: 'Fan, H.', title: 'CHEMBIO 4GG9 Student', project: 'Optimization of Fluoro-ebelactone B Isolation and Purification' },
          { name: 'Nasir, J.', title: 'CHEMBIO 4GG9 Student', project: 'Optimization of Fluoro-ebelactone B Production by Streptomyces' },
          { name: 'Tan, D.', title: 'CHEMBIO 4GG9 Student', project: 'Mass Spectrometric Analysis of Ebelactones' },
          { name: 'Xue, P.', title: 'CHEMBIO 4GG9 Student', project: 'Prebiotic Approach to Cyclocystine' },
          { name: 'Khan, A.', title: 'CHEMBIO 4GG9 Student', project: 'Aldol Approach to Synthesis of Lactone Analogues of Pramanicin' },
          { year: 2016, name: 'Affar, A.', title: 'CHEM 4G09 Student', project: 'Assay and Activity of Fluoro-ebelactone B' },
          { name: 'Shum, R.', title: 'CHEM 4G09 Student', project: 'Synthesis of Lactone Analogues of Pramanicin' },
          { year: 2015, name: 'Liu, P.', title: 'CHEMBIO 4GG9 Student', project: 'Precursor-directed Biosynthesis of Fluoro-ebelactone B' },
          { year: 2014, name: 'Philip, R.', title: 'BIOCHEM 3A03 Student', project: 'Biosynthesis of Ebelactone' },
          { name: 'Wu, B.', title: 'CHEMBIO 4G03 Student', project: 'Synthesis of 4-Fluorobutyric Acid' },
          { year: 2013, name: 'Syed, S.', title: 'ARTS&SCI 3X03 Student', project: 'Precursor-directed Biosynthesis of a Novel Ebelactone' },
          { name: 'Zheng, B.', title: 'CHEMBIO 4G03 Student', project: 'Synthesis of Lorneic Acid and Derivatives' },
          { name: 'Kar, R.', title: 'Co-op Student', project: 'Biosynthesis of Ebelactone' },
          { name: 'Suthagar, W.', title: 'Student Volunteer', project: 'Preparation and Reactions of Ebelactone' },
          { name: 'Syed, S.', title: 'Student Volunteer', project: 'Preparation and Reactions of Ebelactone' },
          { name: 'Arora, S.', title: 'Student Volunteer', project: 'Precursor-directed Biosynthesis of beta-Lactones' },
          { year: 2012, name: 'Zepeda, C.', title: 'PhD Candidate', project: 'Synthesis of beta-lactones' },
          { name: '', title: '', project: '' },
          { name: '', title: '', project: '' },
       ]
    }
  }

  renderTableData() {
    return this.state.members.map((member, index) => {
       const { year, name, title, project } = member //destructuring
       return (
          <tr>
             <td className="tableYear">{year}</td>
             <td className="tableName">{name}</td>
             <td className="tableTitle">{title}</td>
             <td className="tableProject">{project}</td>
          </tr>
       )
    })
  }

  render() {
    return (
      <div className="pContainer">
        <div>

          <Helmet>
            <title>{ TITLE }</title>
          </Helmet>

          <h1 className="pi">PRINCIPAL INVESTIGATOR</h1>

          <section className="piProfile">
            <div className="piProf">
              <img className="piPic" src={require('./../../assets/piprofile.png')}/>
            </div>
            <div className="piDescription">
              <h2>DR. PAUL H.M. HARRISON</h2>
              <div className="piBlock"></div>
              <h3>Associate Professor</h3>
              <h4>CHEMISTRY &#38; CHEMICAL BIOLOGY, MCMASTER UNIVERSITY</h4>
              <h3>Undergraduate Associate Chair</h3>
              <h4>MCMASTER UNIVERSITY</h4>
              <h3 className="piSchool">B.A. (Oxford), Ph.D. (Alberta)</h3>
              <a className="learnMore" href="/Harrison"><h4>LEARN MORE</h4></a>
            </div>
          </section>
        </div>

        <div className="gm">
          <h1 className="gmHeader">GROUP MEMBERS</h1>
          <div className="gmBox">
            <h3 className="gmName">Name</h3>
            <h3 className="gmProject">Project</h3>
            <div className="gmBlock"></div>

            <div>
              <table id='members'>
                <tbody>
                    {this.renderTableData()}
                </tbody>
              </table>
            </div>

            <p1 className="gmOptOut">If you wish to have your name removed from this list, please contact Dr. Harrison at pharriso@mcmaster.ca.</p1>

          </div>
          <div className="groupPic">
            <img src={require('../../assets/grouppic.jpg')}/>
          </div>

        </div>

        
      </div>
    );
  }
}

export default People;