import React, { Component } from 'react';
import './ResearchBiosynthesis.css';
import '../../App.css';
import { Helmet } from 'react-helmet';
import { Parallax } from 'react-parallax';

const TITLE = 'Biosynthesis | Harrison Lab'

const pubList = [
  { pubTitle: "Biosynthesis of ebelactone A: isotopic tracer, advanced precursor and genetic studies reveal a thioesterase-independent cyclization to give a polyketide β-lactone.", pubAuthor: "Wyatt, M., Ahilan, Y., Argyropoulos, P., Boddy, C. N., Magarvey, N. A., Harrison, P. H. M.", pubJournal: "J Antibiot", pubVolume: "66", pubIssuePageDate: "(7), 421–30 (2013)"},
  { pubTitle: "Biosynthesis of Streptolydigin:  Origin of the Oxygen Atoms.", pubAuthor: "Chen, H., Olesen, S. G., Harrison, P. H. M.", pubJournal: "Org Lett", pubVolume: "8", pubIssuePageDate: "(23), 5329-32 (2006)"},
  { pubTitle: "Investigation of the Origin of C2 Units in Biosynthesis of Streptolydigin.", pubAuthor: "Chen, H., Harrison, P. H. M.", pubJournal: "Org Lett", pubVolume: "6", pubIssuePageDate: "(22), 4033-6 (2004)."},
  { pubTitle: "The biosynthesis of pramanicin: Intact incorporation of serine and absolute configuration of the antibiotic.", pubAuthor: "Duspara, P., Jenkins, S. I., Hughes, D. W., Harrison, P. H. M.", pubJournal: "ChemComm", pubVolume: "", pubIssuePageDate: "(23), (1998)"}
];

class ResearchBiosynthesis extends Component {
  render() {

    let biosynthesisText = "Biosynthetic research involves the study of how complex naturally occurring organic molecules are constructed in nature from simple precursors. Our work in this area uses appropriate precursors which have been labelled with isotopes such as deuterium, tritium, carbon-13 and oxygen-18 to study how these molecules are assembled and modified in vivo to generate polyketides, such as the beta-lactone ebelactone shown below. The assembly process is catalyzed by a series of secondary metabolic enzymes that join the correct units and control the substituents on the carbon chain, e.g. alcohol and ketone groups in ebelactone. We have identified the biosynthetic gene cluster for ebelactone. The combination of these two approaches allows a detailed analysis of the assembly process. \nLastly, modified precursors can be added to the Streptomyces culture that produces ebelactone, and in some cases these precursors can be carried through the assembly line to generate structurally modified natural products. These so-called “non-natural natural products” can potentially have greater biological potency, e.g. as antibiotics. We are investigating whether such an analogue of ebelactone can be developed for greater potency against the targets of ebelactone such as esterases. \nThis research involves a mixture of organic synthesis to make the prerequisite labelled compounds, microbiology to grow antibiotic-producing micro-organisms, enzymology, chemical purification of the product, and extensive use of NMR to locate the label."

    let biosynthesisSummary = biosynthesisText.split('\n').map(i => {
      return <p className="biosynthesisText">{i}</p>
    });

    return (

      <div>

        <Helmet>
          <title>{ TITLE }</title>
        </Helmet>

        <Parallax
            blur={0}
            bgImage={require('../../assets/biosynthbg.jpg')}
            bgImageAlt="Biosynthesis Research Background Image"
            strength={900}
        >
            <h1 className="RBtitle ResearchTitle">BIOSYNTHESIS</h1>
            <div style={{ height: '100px' }} />
        </Parallax>

        <section className="ResearchContainer">
          <h3 className="biosynthesisText">{ biosynthesisSummary }</h3>
          <img className="biosynthFig" src={require('../../assets/biosynthfig.png')}/>
        </section>


        <div className="biosynthesisPub ResearchPub">
          <h1 className="RBPubTitle PubTitle">PUBLICATION HIGHLIGHTS</h1>
        </div>
        <div className="pubContainer">
          <ol reversed className="pubList">
            {pubList.map(
              (pubListing, index) => (
                
                <li key={index} className="pubListing">
                  <div className="pubTitle">{ pubListing.pubTitle }</div>
                  <div>
                    <p1>{pubListing.pubAuthor}</p1>
                    <p1 className="pubJournal"> {pubListing.pubJournal}</p1>
                    <p1 className="pubVolume"> {pubListing.pubVolume}</p1>
                    <p1> {pubListing.pubIssuePageDate}.</p1>
                  </div>
                </li>
              )
            )}
          </ol>
        </div>
      </div>
    );
  }
}

export default ResearchBiosynthesis;