import React, { Component } from 'react';
import './Home.css';
import { Link } from 'react-router-dom';

class Home extends Component {
  render() {
    let headingtop = "WELCOME TO"
    let heading = "HARRISON LAB"
    let subheading = "Fooling the biosynthetic machinery into using non-natural precursors to make non-natural, natural products."
    let researchOverview = "Chemical biology is the exciting area where chemists and life scientists overlap to tackle biological problems by means of atomic understanding. Our research in this area focuses on natural products from secondary metabolism. Secondary metabolites include many compounds important to man, such as antibiotics, perfumes, flavouring agents, toxins and narcotics. We are interested in three main areas:"
    let topic1 = "Biosynthesis"
    let topic2 = "Antibiotic Mechanisms of Action"
    let topic3 = "Biomimetic Synthesis"

    return (
      <div>
        <section className="hero">
          <div className="hero-body">
            <div className="container">
              <h3 className="titletop">{ headingtop }</h3>
              <h6 className="title">{ heading }</h6>
              <h1 className="titlebot">{ subheading }</h1>
            </div>
          </div>
        </section>
      
        <section className="research-overview">
          <div className="section-block"></div>
          <h3 className="researchsummary">{ researchOverview }</h3>
        </section>

        <section className="research-topics">
          <div className="topic" >
            <Link to="/harrison/biosynthesis"><h2 className="topictext">{ topic1 }</h2></Link>
          </div>
          <div className="topic" >
            <Link to="/harrison/antibiotic-mechanisms-of-action"><h2 className="topictext">{ topic2 }</h2></Link>
          </div>
          <div className="topic" >
            <Link to="/harrison/biomimetic-synthesis"><h2 className="topictext">{ topic3 }</h2></Link>
          </div>
        </section>
      
      </div>

    );
  }
}

export default Home;