class Publications extends Component {
  render() {


    return (
      <div>
        
        <Helmet>
          <title>{ TITLE }</title>
        </Helmet>

        <div className="pubContainer">
          <h1 className="pubHeader">PUBLICATIONS</h1>
        
          <ol reversed className="pubList">
          {pubList.map(
            (pubListing, index) => (
              
              <li key={index} className="pubListing">
                <div className="pubTitle">{ pubListing.pubTitle }</div>
                <div>
                  <p1>{pubListing.pubAuthor}</p1>
                  <p1 className="pubJournal"> {pubListing.pubJournal}</p1>
                  <p1 className="pubVolume"> {pubListing.pubVolume}</p1>
                  <p1> {pubListing.pubIssuePageDate}.</p1>
                </div>
              </li>
              
            )
            
          )}
          </ol>
          
        
        </div>

      </div>
    );
  }
}

export default Publications;