import React, { Component } from 'react';
import './App.css';
import Header from './components/Header/Header';
import Footer from './components/Footer/Footer';
import Home from './components/Home/Home';
import ResearchAntibiotic from './components/ResearchAntibiotic/ResearchAntibiotic';
import ResearchBiomimetic from './components/ResearchBiomimetic/ResearchBiomimetic';
import ResearchBiosynthesis from './components/ResearchBiosynthesis/ResearchBiosynthesis';
import People from './components/People/People';
import Publications from './components/Publications/Publications';
import Harrison from './components/Harrison/Harrison';
import { Route } from 'react-router-dom';


class App extends Component {
  
  render() {
    return (
      <div className="App">
        
        <Header />

          <Route exact={true} path="/harrison" component={Home} />
          <Route path="/harrison/people" component={People} />
          <Route path="/harrison/biosynthesis" component={ResearchBiosynthesis} />
          <Route path="/harrison/biomimetic-synthesis" component={ResearchBiomimetic} />
          <Route path="/harrison/antibiotic-mechanisms-of-action" component={ResearchAntibiotic} />
          <Route path="/harrison/publications" component={Publications} />
          <Route path="/harrison/pharrison" component={Harrison} />
        <Footer />
      </div>
    );
  }
}

export default App;
