import React, { Component } from 'react';
import './ResearchAntibiotic.css';
import '../../App.css';
import { Helmet } from 'react-helmet';
import { Parallax } from 'react-parallax';

const TITLE = 'Antibiotic Mechanisms of Action | Harrison Lab'

const pubList = [
  { pubTitle: "Pramanicin Analog Induces Apoptosis in Human Colon Cancer Cells: Critical Roles for Bcl-2, Bim, and p38 MAPK Signaling.", pubAuthor: "Bodur, C., Kutuk, O., Karsli-Uzunbas, G., Isimjan, T. T., Harrison, P. H. M., Basaga, H.", pubJournal: "PLoS One", pubVolume: "8", pubIssuePageDate: "(2), e56369 (2013)"},
  { pubTitle: "β-Lactone Natural Products and Derivatives Inactivate Homoserine Transacetylase, a Target for Antimicrobial Agents.", pubAuthor: "Pascale, G. D., Nazi, I., Harrison, P. H. M., Wright, G. D.", pubJournal: "J Antibiot", pubVolume: "64", pubIssuePageDate: "(7), 483-7 (2011)"},
  { pubTitle: "Pramanicin, an antifungal agent, raises cytosolic Ca2+ and causes cell death in vascular endothelial cells.", pubAuthor: "Kwan, C. Y., Harrison, P. H. M., Kwan, T. K.", pubJournal: "Curr Vasc Pharmacol", pubVolume: "40", pubIssuePageDate: "(1), 35-42 (2003)"},
  { pubTitle: "Vasorelaxant Effects of Pramanicin, an Antifungal Agent: Selective Action on Endothelial Cells.", pubAuthor: "Kwan, C. Y., Harrison, P. H. M., Duspara, P. A., Daniel, E. E.", pubJournal: "Jpn J Pharmacol", pubVolume: "85", pubIssuePageDate: "(3), 234-40 (2001)"}
];


class ResearchAntibiotic extends Component {
  render() {

    let antibioticText = "Beta-Lactones such as ebelactone (see “Biosynthesis”) and F-244 are unusually reactive esters as a consequence of the ring strain in the 4-membered ring. As such, they can react with nucleophilic groups in target enzymes to form covalent adducts, resulting in irreversible inactivation of the enzyme.  Ebelactone reacts with esterases, lipases and cutinases, while F-244 targets HMG-CoA synthase, an enzyme responsible for cholesterol biosynthesis. In the latter case, therefore, F-244 has potential application for lowering cholesterol levels in humans. We have studied this inactivation process by preparing a series of analogues and comparing their ability to inactivate HMG-CoA synthase. More recently, we have directed our attention to studying the mechanism of inactivation of esterase by ebelactone, in order to then compare activity with the analogues made by incorporation of non-natural precursors (see “Biosynthesis”)."

    let antibioticSummary = antibioticText.split('\n').map(i => {
      return <p className="antibioticText">{i}</p>
    });

    return (
      <div>

        <Helmet>
          <title>{ TITLE }</title>
        </Helmet>

        <Parallax
            blur={0}
            bgImage={require('../../assets/antibioticbg.jpg')}
            bgImageAlt="Antibiotic Mechanisms of Action Research Background Image"
            strength={900}
        >
            <h1 className="RAtitle ResearchTitle">ANTIBIOTIC MECHANISMS OF ACTION</h1>
            <div style={{ height: '100px' }} />
        </Parallax>

        <section className="ResearchContainer">
          <h3 className="antibioticText">{ antibioticSummary }</h3>
        </section>
        <section className="antibioticPub ResearchPub">
          <h1 className="RBPubTitle PubTitle">PUBLICATION HIGHLIGHTS</h1>
        </section>
        <div className="pubContainer">
          <ol className="pubList">
            {pubList.map(
              (pubListing, index) => (
                
                <li key={index} className="pubListing">
                  <div className="pubTitle">{ pubListing.pubTitle }</div>
                  <div>
                    <p1>{pubListing.pubAuthor}</p1>
                    <p1 className="pubJournal"> {pubListing.pubJournal}</p1>
                    <p1 className="pubVolume"> {pubListing.pubVolume}</p1>
                    <p1> {pubListing.pubIssuePageDate}.</p1>
                  </div>
                </li>
              )
            )}
          </ol>
        </div>
      </div>
    );
  }
}

export default ResearchAntibiotic;