import React, { Component } from 'react';
import './ResearchBiomimetic.css';
import '../../App.css';
import { Helmet } from 'react-helmet';
import { Parallax } from 'react-parallax';

const TITLE = 'Biomimetic Synthesis | Harrison Lab'

const pubList = [
  { pubTitle: "Studies of structure and dynamics in a nominally symmetric twisted amide by NMR and electronic structure calculations.", pubAuthor: "Bain, A. D., Chen, H., Harrison, P. H. M.", pubJournal: "Can J Chem", pubVolume: "84", pubIssuePageDate: "(3), 421-8 (2006)"},
  { pubTitle: "Regioselectivity of glycoluril-directed Claisen condensations — A kinetic and mechanistic study of substituent effects in the nucleophilic acyl group.", pubAuthor: "Chen, M., Won, K., McDonald, R. S., Harrison, P. H. M.", pubJournal: "Can J Chem", pubVolume: "84", pubIssuePageDate: "(9), 1188-96 (2006)"},
  { pubTitle: "Biomimetic decarboxylative condensation between malonate and acetate units attached to a glycoluril template.", pubAuthor: "Chen, H., Harrison, P. H. M.", pubJournal: "Can J Chem", pubVolume: "80", pubIssuePageDate: "(6), 601-7 (2002)"},
  { pubTitle: "Twisted Amides: Synthesis and Structure of 1,6-Dipivaloyl-3,4,7,8- tetramethyl-2,5-dithioglycoluril.", pubAuthor: "Duspara, P. A., Matta, C. F., Jenkins, S. I., Harrison, P. H. M.", pubJournal: "Org Lett", pubVolume: "3", pubIssuePageDate: "(4), 495-8 (2001)"},
  { pubTitle: "A Facile Preparation of Thioglycolurils from Glycolurils, and Regioselectivity in Thioglycoluril Template-Directed Crossed-Claisen Condensations.", pubAuthor: "Cow, C. N., Harrison, P. H. M.", pubJournal: "J Org Chem", pubVolume: "62", pubIssuePageDate: "(25), 8834-40 (1997)"},
  { pubTitle: "Repetitive Template-Directed Acyl Transfer to Mimic Steps in the Biosynthesis of Polyketides and Fatty Acids.", pubAuthor: "Sun, S., Harrison, P. H. M.", pubJournal: "ChemComm", pubVolume: "", pubIssuePageDate: "(1994)"},
];

class ResearchBiomimetic extends Component {
  render() {

    let biomimeticText = "Living organisms effect a wide variety of chemical transformations in a remarkably efficient and selective manner: many reactions in research and industry utilize naturally occurring enzymes or micro-organisms. We are learning from nature how to achieve efficient and selective Claisen condensations, such as occur naturally in the formation of the polyketides such as ebelactone and F-244 (see “Mechanisms of action” segment), in a purely chemical fashion. Specifically, the research involves the intramolecular Claisen-like condensation of two acyl units which have been attached to a single glycoluril ''template'' molecule. We have demonstrated that the glycoluril template [3] can be sequentially acylated to give derivatives [4], which undergo high-yielding and fast intramolecular Claisen-like condensations to give [5] under exceptionally mild conditions (Figure below). This system models some of the features of the enzymic process, and allows for iterative addition-condensation sequences to give long carbon chains; for example, we prepared an 8-carbon chain by joining together four 2-carbon acetate units. \nOther variations allow control over the regiochemical outcome of the intramolecular Claisen condensation.  Understanding of the mechanism of this reaction is being developed through kinetic analysis of the reaction, and via X-ray crystal structures of these glycolurils; the latter work has generated an interesting example of a molecule with two twisted amide functional groups. \nThis project involves extensive preparative organic synthesis, as well as the use of NMR and other spectroscopic techniques for analysis of products."

    let biomimeticSummary = biomimeticText.split('\n').map(i => {
      return <p className="biomimeticText">{i}</p>
    });

    return (
      <div>

        <Helmet>
          <title>{ TITLE }</title>
        </Helmet>

        <Parallax
            blur={0}
            bgImage={require('../../assets/biomimeticbg.jpg')}
            bgImageAlt="Biomimetic Synthesis Research Background Image"
            strength={900}
        >
            <h1 className="RBMtitle ResearchTitle">BIOMIMETIC SYNTHESIS</h1>
            <div style={{ height: '100px' }} />
        </Parallax>

        <section className="ResearchContainer">
          <h3 className="biomimeticText">{ biomimeticSummary }</h3>
          <img className="biomimeticFig" src={require('../../assets/biomimeticfig.png')}/>
          <h3 className="biomimeticCap">1. (RCH<sub>2</sub>CO)<sub>2</sub>O, refluxOR: LDA, then RCH<sub>2</sub>COCl</h3>
          <h3 className="biomimeticCap">2. LDA, then CH<sub>3</sub>COCl</h3>
          <h3 className="biomimeticCap">3. <i>tert</i>-BuOLi, THF, 20 min, 0&#176;C, 90% yields</h3>
        </section>
        
        <div className="biomimeticPub ResearchPub">
          <h1 className="RBMPubTitle PubTitle">PUBLICATION HIGHLIGHTS</h1>
        </div>
        
        <div className="pubContainer">
          <ol className="pubList">
            {pubList.map(
              (pubListing, index) => (
                
                <li key={index} className="pubListing">
                  <div className="pubTitle">{ pubListing.pubTitle }</div>
                  <div>
                    <p1>{pubListing.pubAuthor}</p1>
                    <p1 className="pubJournal"> {pubListing.pubJournal}</p1>
                    <p1 className="pubVolume"> {pubListing.pubVolume}</p1>
                    <p1> {pubListing.pubIssuePageDate}.</p1>
                  </div>
                </li>
              )
            )}
          </ol>
        </div>
      
      </div>
    );
  }
}

export default ResearchBiomimetic;